<template>
    <div>
        <!-- 工具栏 -->
        <div>
            <el-input prefix-icon="el-icon-plus" size="small" v-model="jl.name"
                      style="width: 300px" placeholder="添加职称...">
            </el-input>
            <el-select style="margin-left: 5px; margin-right: 5px" v-model="jl.titleLevel" placeholder="职称等级"
                       size="small">
                <el-option
                        v-for="item in titleLevels"
                        :key="item"
                        :label="item"
                        :value="item">
                </el-option>
            </el-select>
            <el-button size="small" icon="el-icon-plus" @click="addJobLevel" type="primary">添加</el-button>
        </div>
        <!-- 表格 -->
        <div style="margin-top: 10px">
            <el-table
                    v-loading="loading"
                    element-loading-text="正在加载..."
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"

                    :data="jls"
                    stripe
                    border
                    size="small"
                    @selection-change="handleSelectionChange"
                    style="width: 80%">
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="支撑名称"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="titleLevel"
                        label="职称级别">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间">
                </el-table-column>
                <el-table-column
                        label="是否启用">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.enabled">以启用</el-tag>
                        <el-tag type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        label="操作">
                    <template slot-scope="scope">
                        <el-button size="small" @click="showEditView(scope.row)">编辑</el-button>
                        <el-button size="small" type="danger" @click="deleteHandler(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="danger" size="small" style="margin-top: 10px" :disabled="multipleSelection.length === 0" @click="deleteMany">批量删除</el-button>
        </div>
        <el-dialog
                title="修改职称"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <table>
                    <tr>
                        <td>
                            <el-tag>职称名</el-tag>
                        </td>
                        <td>
                            <el-input size="small" v-model="updateJl.name"></el-input>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <el-tag>职称级别</el-tag>
                        </td>
                        <td>
                            <el-select style="margin-left: 5px; margin-right: 5px" v-model="updateJl.titleLevel" placeholder="职称等级"
                                       size="small">
                                <el-option
                                        v-for="item in titleLevels"
                                        :key="item"
                                        :label="item"
                                        :value="item">
                                </el-option>
                            </el-select>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <el-tag >是否启用</el-tag>
                        </td>
                        <td>
                            <el-switch
                                    v-model="updateJl.enabled"
                                    active-text="启用"
                                    inactive-text="禁用">
                            </el-switch>
                        </td>
                    </tr>
                </table>
            </div>
            <span slot="footer" class="dialog-footer">
            <el-button size="small" @click="dialogVisible = false">取 消</el-button>
            <el-button size="small" type="primary" @click="doUpdate">确 定</el-button>
            </span>
        </el-dialog>
    </div>

</template>

<script>

    export default {
        name: "JobLevelMana",
        data() {
            return {
                loading: false,  // 进度条
                // 对话框默认是关闭的
                dialogVisible: false,
                updateJl: {
                    name: '',
                    titleLevel: '',
                    enabled: false
                },
                jl: {
                    name: '',
                    titleLevel: ''
                },
                jls: [],
                titleLevels: [
                    '正高级',
                    '副高级',
                    '中级',
                    '初级',
                    '员级',
                ],
                multipleSelection: [],
            }
        },
        mounted() {
            this.initJls();
        },
        methods: {
            handleSelectionChange(val){
                // 批量删除的括号
                this.multipleSelection = val;
            },
            deleteMany(){
                // 批量删除
                this.$confirm('此操作将永久删除【' + this.multipleSelection.length + '】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '?';
                    // item 里面包含一条数据的所有信息
                    this.multipleSelection.forEach(item => {
                        ids += 'ids=' + item.id + '&';
                    })
                    this.deleteRequest("/system/basic/joblevel/many/" + ids)
                        .then(resp => {
                            if (resp) {
                                this.initJls();
                            }
                        })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            showEditView(data) {
                // 给修改对话框赋 “旧” 值
                Object.assign(this.updateJl, data);
                // 显示更修对话框
                this.dialogVisible = true;
            },
            doUpdate() {
                // 点击确定之后修改数据
                this.putRequest("/system/basic/joblevel/", this.updateJl)
                .then(resp => {
                    if (resp) {
                        this.initJls(); // 刷新表格
                        this.dialogVisible = false; // 关闭修改对话框
                    }
                })
            },
            deleteHandler(data) {
                // data 是当前这一行数据的所有属性， name、id、createDate 等
                // console.log(data)
                this.$confirm('此操作将永久删除【' + data.name + '】职称, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/joblevel/" + data.id)
                        .then(resp => {
                            if (resp) {
                                this.initJls();
                            }
                        })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            addJobLevel() {
                if (this.jl.name && this.jl.titleLevel) {
                    this.postRequest("/system/basic/joblevel/", this.jl)
                        .then(resp => {
                            if (resp) {
                                this.initJls();
                                this.jl = {
                                    name: '',
                                    titleLevel: ''
                                }
                            }
                        })
                } else {
                    this.$message.error("添加字段不能为空！")
                }
            },
            initJls() {
                this.loading = true;
                this.getRequest("/system/basic/joblevel/").then(resp => {
                    this.loading = false;
                        if (resp) {
                            this.jls = resp;
                        }
                    })
            }
        }
    }
</script>

<style scoped>

</style>