<template>
    <div>
        <div>
            <el-input
                    v-loading="loading"
                    element-loading-text="正在加载..."
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"

                    size="small"
                    class="addPosInput"
                    placeholder="添加职位..."
                    prefix-icon="el-icon-plus"
                    @keydown.enter.native="addPosition"
                    v-model="pos.name">
            </el-input>
            <el-button
                    size="small" type="primary"
                    icon="el-icon-plus"
                    @click="addPosition"
            >添加
            </el-button>
        </div>
        <div class="posManaMain">
            <el-table
                    :data="positions"
                    stripe
                    border
                    @selection-change="handleSelectionChange"
                    size="small"
                    style="width: 70%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职位名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        width="150"
                        label="创建时间">
                </el-table-column>
                <el-table-column
                        label="是否启用">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.enabled">以启用</el-tag>
                        <el-tag type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button @click="showEditView(scope.$index, scope.row)" size="mini">编辑</el-button>
                        <el-button type="danger" @click="handleDelete(scope.$index, scope.row)" size="mini">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button size="small" style="margin-top: 8px" type="danger" @click="deleteMany"
                       :disabled="multipleSelection.length === 0"> 批量删除
            </el-button>
        </div>
        <el-dialog
                title="修改职位"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <div>
                    <el-tag>职位名称</el-tag>
                    <el-input @keydown.enter.native="doUpdate" class="updatePosInput" size="small"
                              v-model="updatePos.name"></el-input>
                </div>
                <div>
                    <el-tag style="margin-top: 5px; margin-right: 10px">是否启用</el-tag>
                    <el-switch
                            v-model="updatePos.enabled"
                            active-text="启用"
                            inactive-text="禁用">
                    </el-switch>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
            <el-button size="small" @click="dialogVisible = false">取 消</el-button>
            <el-button size="small" type="primary" @click="doUpdate">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "PosMana",
        data() {
            return {
                loading: false,
                pos: {
                    name: ''
                },
                multipleSelection: [],
                dialogVisible: false,
                // 点击修改框后，在修改框里显示的老信息
                updatePos: {
                    name: '',
                    enabled: false
                },
                positions: []
            }
        },
        // 在组件初始化的时候 mounted() 就会运行
        mounted() {
            this.initPositions();
        },
        methods: {
            deleteMany() {
                this.$confirm('此操作将永久删除【' + this.multipleSelection.length + '】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '?';
                    this.multipleSelection.forEach(item => {
                        ids += '&ids=' + item.id + '&';  // 拼接批量删除 url 地址
                    })
                    this.deleteRequest("/system/basic/pos/" + ids)
                        .then(resp => {
                            if (resp) {
                                this.initPositions();
                            }
                        });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            // 批量删除： 没有点击复选框时，是不能点击的
            handleSelectionChange(val) {
                // multipleSelection 保存的就是目前选中的项目
                this.multipleSelection = val
                // console.log(this.multipleSelection)
            },
            addPosition() {
                // 有没有输入值,没有就提示
                if (this.pos.name) {
                    this.postRequest("/system/basic/pos/", this.pos)
                        .then(resp => {
                            if (resp) {
                                // 添加完成之后,刷新列表
                                this.initPositions();
                                // 清空输入框
                                this.pos.name = ''
                            }
                        })
                } else {
                    this.$message.error("职位名称不可以为空")
                }
            },
            // 点击 修改按钮的事件
            showEditView(index, data) {
                // this.updatePos = data;
                // 这个方法可以让 输入框和列表不同步，不然署副矿你输入一个字符 列表就同步发生变化，取消修改时也会被缓存
                Object.assign(this.updatePos, data);
                // 显示对话框
                this.dialogVisible = true;
            },
            handleDelete(index, data) {
                this.$confirm('此操作将永久删除【' + data.name + '】职位, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/pos/" + data.id)
                        .then(resp => {
                            if (resp) {
                                this.initPositions();
                            }
                        });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            doUpdate() {
                this.putRequest("/system/basic/pos/", this.updatePos)
                    .then(resp => {
                        if (resp) {
                            // 更新成功， 更新列表
                            this.initPositions();
                            // 复原对话框的 输入框
                            this.updatePos.name = '';
                            // 隐藏对话框
                            this.dialogVisible = false;
                        }
                    })
            },
            initPositions() {
                this.loading = true;
                this.getRequest("/system/basic/pos/").then(resp => {
                    this.loading = false;
                        if (resp) {
                            this.positions = resp;
                        }
                    })
            }
        }

    }
</script>

<style>
    .addPosInput {
        width: 300px;
        margin-right: 8px
    }

    .updatePosInput {
        width: 200px;
        margin-left: 8px;
    }

    .posManaMain {
        margin-top: 10px;
    }
</style>