<template>
    <div>
        <div style="margin-top: 10px; display: flex;justify-content: center">
            <el-input v-model="keywords" placeholder="通过用户名搜索用户..." prefix-icon="el-icon-search"
                      style="width: 400px;margin-right: 10px" @keydown.enter.native="doSearch"></el-input>
            <el-button icon="el-icon-search" @click="doSearch" type="primary">搜索</el-button>
        </div>
        <div class="hr-container">
            <el-card class="he-card" v-for="(hr, index) in hrs" :key="index">
                <div slot="header" class="clearfix">
                    <span>{{hr.name}}</span>
                    <el-button style="float: right; padding: 3px 0;color: #e70707" type="text"
                               icon="el-icon-delete" @click="doDelete(hr)"></el-button>
                </div>
                <div>
                    <div class="img-container">
                        <img :src="hr.userFace" :alt="hr.name" :title="hr.name" class="userface-img">
                    </div>
                    <div class="userinfo-container">
                        <div>用户名: {{hr.name}}</div>
                        <div>手机号码: {{hr.phone}}</div>
                        <div>电话号码: {{hr.telephone}}</div>
                        <div>地址: {{hr.address}}</div>
                        <div>用户状态:
                            <el-switch
                                    v-model="hr.enabled"
                                    active-text="启用"
                                    @change="enabledChange(hr)"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    inactive-text="禁用"
                            ></el-switch>
                        </div>
                        <div>用户角色:
                            <el-tag type="success" style="margin-right: 4px" v-for="(role, indexj) in hr.roles"
                                    :key="indexj">{{role.nameZh}}
                            </el-tag>
                            <el-popover
                                    placement="right"
                                    title="角色列表"
                                    @show="showPop(hr)"
                                    @hide="hidePop(hr)"
                                    width="200"
                                    trigger="click">
                                <!-- 选中哪个 :value="r.id" 就会把 r.id 赋值给 selectedRoles  -->
                                <el-select v-model="selectedRoles" multiple placeholder="请选择">
                                    <el-option
                                            v-for="(r,indexk) in allRoles"
                                            :key="indexk"
                                            :label="r.nameZh"
                                            :value="r.id">
                                    </el-option>
                                </el-select>
                                <el-button slot="reference" icon="el-icon-more" type="text"></el-button>
                            </el-popover>

                        </div>
                        <div>备注：{{hr.remark}}</div>
                    </div>
                </div>
            </el-card>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SysHr",
        data() {
            return {
                keywords: '',
                hrs: [],
                allRoles: [],
                selectedRoles: []
            }
        },
        mounted() {
            this.initHrs();
        },
        methods: {
            doDelete(hr) {
                this.$confirm('此操作将永久删除【' + hr.name + '】, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/hr/" + hr.id)
                    .then(resp => {
                        if (resp) {
                            this.initHrs();
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            doSearch() {
                this.initHrs();
            },
            hidePop(hr) {
                // 先判断，选中的那些角色有没有改动，没有改动就不更新
                let roles = [];
                Object.assign(roles, hr.roles);  // 把 hr.roles 拷贝到 roles 空数组上来
                let flag = false;
                // 判断是否相等
                if (roles.length !== this.selectedRoles.length) {
                    flag = true;
                } else {
                    for (let i = 0; i < roles.length; i++) {
                        let role = roles[i];
                        for (let j = 0; j < this.selectedRoles.length; j++) {
                            let sr = this.selectedRoles[j];
                            if (role.id === sr) {
                                roles.splice(i, 1);
                                i--;  // 不然会跳掉一个数据
                                break;
                            }
                        }
                    }
                    if (roles.length !== 0) {
                        flag = true;
                    }
                }
                if (flag) {
                    let url = '/system/hr/role?hrid=' + hr.id;
                    this.selectedRoles.forEach(sr => {
                        url += '&rids=' + sr;
                    });
                    this.putRequest(url).then(resp => {
                        if (resp) {
                            this.initHrs();
                        }
                    })
                }
            },
            showPop(hr) {
                this.initAllRoles();
                let roles = hr.roles;
                this.selectedRoles = [];
                roles.forEach(r => {
                    this.selectedRoles.push(r.id);
                });
            },
            initAllRoles() {
                this.getRequest("/system/hr/roles/").then(resp => {
                    if (resp) {
                        this.allRoles = resp;
                    }
                })
            },
            enabledChange(hr) {
                delete hr.roles;  // 删除 roles 属性，不然服务端会报错
                this.putRequest("/system/hr/", hr).then(resp => {
                    if (resp) {
                        this.initHrs();
                    }
                })
            },
            initHrs() {
                this.getRequest("/system/hr/?keywords=" + this.keywords).then(resp => {
                    if (resp) {
                        this.hrs = resp;
                    }
                })
            }
        }
    }
</script>

<style>
    .hr-container {
        margin-top: 10px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    .he-card {
        width: 350px;
        margin-bottom: 20px;
    }

    .userface-img {
        width: 72px;
        height: 72px;
        border-radius: 72px;
    }

    .img-container {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .userinfo-container {
        margin-top: 20px;
    }

    .userinfo-container div {
        font-size: 12px;
        color: #3965e7;
    }

</style>