<template>
    <div style="width: 500px">
        <el-input
                prefix-icon="el-icon-search"
                placeholder="输入部门名称进行搜索"
                v-model="filterText">
        </el-input>

        <el-tree
                :data="deps"
                :props="defaultProps"
                :filter-node-method="filterNode"
                :expand-on-click-node="false"
                ref="tree">
            <span class="custom-tree-node" style="display: flex;justify-content: space-between;width: 100%" slot-scope="{ node, data }">
        <span>{{ data.name }}</span>
        <span>
          <el-button
                  class="depButton"
                  type="primary"
                  size="mini"
                  @click="() => showAddDepView(data)">
            添加部门
          </el-button>
          <el-button
                  class="depButton"
                  type="danger"
                  size="mini"
                  @click="() => deleteDep(data)">
            删除部门
          </el-button>
        </span>
      </span>
        </el-tree>
        <el-dialog
                title="添加部门"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <table>
                    <tr>
                        <td><el-tag>上级部门</el-tag></td>
                        <td><span>{{pName}}</span></td>
                    </tr>
                    <tr>
                        <td><el-tag>部门名称</el-tag></td>
                        <td><el-input size="mini" v-model="dep.name" placeholder="请输入部门名称..."></el-input></td>
                    </tr>
                </table>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doAdd">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "DepMana",
        data() {
            return {
                deps: [],
                filterText: '',
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                dialogVisible: false,
                // 要添加的对象
                dep: {
                    name: '',  // 添加的部门名称
                    parentId: -1,  // 父部门 id
                },
                pName: ''
            }
        },
        watch: {
          filterText(val) {
              this.$refs.tree.filter(val);
          }
        },
        mounted() {
            this.initDeps();
        },
        methods: {
            initDep() {
                this.dep={
                    name: '',  // 添加的部门名称
                    parentId: -1,  // 父部门 id
                };
                this.pName ='';
            },
            addDep2Deps(deps, dep) {
                /**
                 * deps 是已经在书目录显示的数据
                 * dep 是新从数据库获取的数据
                 * 遍历老数据，找到合适的位置 把新的数据 插进去即可！
                 *      如果已存在的一条数据 id 和新接收数据数据的 parentId 相等
                 *      那么直接插入(concat())到老数据后面即可；
                 */
              for (let i=0; i < deps.length; i++) {
                  let d = deps[i];
                  if (d.id === dep.parentId) {
                      d.children = d.children.concat(dep);
                      if (d.children.length >= 0) {
                          d.parent =  true;
                      }
                      return;
                  } else {
                      this.addDep2Deps(d.children, dep);
                  }
              }
            },
            doAdd() {
              this.postRequest("/system/basic/department/", this.dep)
                .then(resp => {
                    if (resp) {
                        /**
                         *  点击确定添加部门之后，如果直接 this.initDeps(); 的话
                         *  添加成功之后树目录会自动叠回去，对用户体验不好
                         *  所以用递归解决
                         */
                        this.addDep2Deps(this.deps, resp.obj)
                        this.dialogVisible = false;
                        this.initDep();  // 清空输入框
                    }
                })
            },
            showAddDepView(data) {
                // 获取 parentName
                this.pName = data.name;
                this.dep.parentId = data.id;
                this.dialogVisible = true;
            },
            removeDepFromDeps(p, deps, id) {
                for (let i=0; i < deps.length; i++) {
                    let d = deps[i];
                    if (d.id === id) {
                        deps.splice(i,1);  // 从 i 开始删，删一个
                        if (deps.length === 0) {
                            p.parent = false;
                        }
                        return;
                    } else {
                        // 如果没有，继续遍历
                        this.removeDepFromDeps(d, d.children, id);
                    }
                }
            },
            deleteDep(data) {
                // isParent 在这里是 parent， 后端返回来的就是 parent
                if (data.parent) {
                    this.$message.error("父部门删除失败!")
                } else {
                    this.$confirm('此操作将永久删除【'+ data.name +'】部门, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.deleteRequest("/system/basic/department/" + data.id)
                        .then(resp => {
                            if (resp) {
                                this.removeDepFromDeps(null, this.deps, data.id);
                            }
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                }
            },
            initDeps() {
              this.getRequest("/system/basic/department/")
                .then(resp => {
                    if (resp) {
                        this.deps = resp;
                    }
                })
            },
            /**
             * value 是传进来的数据， data 是从后端传来的 json 对象
             * 返回 true 显示 data，
             * 返回 false 不显示 data
             */
            filterNode(value, data) {
                if (!value) return true;
                // 如果 传来的 json 对象的 name 包含 value 那么返回 true
                return data.name.indexOf(value) !== -1;
            }
        }
    }
</script>

<style>

    .depButton {
        padding: 2px;
    }

</style>